<template>
  <div id="app">
    <!--<video controls controlsList="nodownload" oncontextmenu="return false;">-->
    <video controls preload poster="https://c.s-microsoft.com/zh-tw/CMSImages/Surface_Where2buy_Hero_JL_V3.jpg?version=15923625-15a3-6fbd-a28c-0a0d89b960e3" controlsList="nodownload">
      <source :src="videoSrc" type="video/mp4">
    </video>
  </div>
</template>

<script>

export default {
  name: 'app',
  data: function () {
    return {
      vidoeId: this.getParameterByName('id'),
    }
  },
  computed: {
    videoSrc: function () {
      return `https://s3-ap-northeast-1.amazonaws.com/microsoft-videos/${this.vidoeId}.mp4`
    },
  },
  methods: {
    getParameterByName: function (name, url) {
      if (!url) {
        url = window.location.href
      }
      name = name.replace(/[\[\]]/g, "\\$&")
      const regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)")
      const results = regex.exec(url)
      if (!results) {
        return null
      }
      if (!results[2]) {
        return ''
      }

      return decodeURIComponent(results[2].replace(/\+/g, " "))
    },
  },
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100vw;
  height: 100vh;
  background-image: url(http://guldfasanen4.se/wordpress/wp-content/uploads/2015/05/white-room-background_web-1024x607.jpg);
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

video {
  width: 640px;
  height: 420px;
}
</style>



